<template>
  <div class="single-blog">
  	<h3>{{blog.title}}</h3>
  	<article>{{blog.content}}</article>
  	<p>作者：{{blog.author}}</p>
  	<p>分类</p>
  	<ul>
  		<li v-for="categorie in blog.categories">{{categorie}}</li>
  	</ul>

  </div>
</template>

<script>
export default {
  name: 'Single-Blog',
  data () {
    return {
    	id:this.$route.params.id,
    	blog:[]
    }
  },
  created(){
  	this.$http.get("https://wd3930139874jiaqqv.wilddogio.com/posts/"+this.id+".json").then(function (data) {
  		   this.blog=data.body
  		   console.log(data.json());
  	})
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
   .single-blog{
   	   max-width: 1000px;
    /*	width: 700px;*/
    	margin: 0 auto; 
    	padding: 30px;
    	background: lightgray;
   }
</style>



