<template>
	<nav>
		<ul>
			<li><router-link to="/" exact>博客展示</router-link></li>
		    <li><router-link to="/add" exact>添加博客</router-link></li>
	    </ul>
	</nav>
</template>



<script type="text/javascript">
	export default{
		name:"blog-header",
	}
</script>


<style scoped>
    a{
    	text-decoration-line: none;
		color: white;
		padding: 12px;
		border-radius: 5px;
    }
    nav{
		background: red;
		    	max-width: 1000px;
    /*	width: 700px;*/
    	margin: 0 auto; 
    	padding: 30px;

    }
	ul{
		list-style-type: none;
		text-align: center;
		font-size: 26px;
		margin: 0;
	}
	li{
		display: inline-block;
		margin: 0 10px;
	}
	.router-link-active{
		background: rgba(255,255,255,.3);
		color:black;
	}
</style>


