<template>
    <div id="addblog">
  	    <h2>添加博客</h2>
	  	<form v-if="!submit">
	  		<label>博客主题</label><input type="text" v-model="blog.title">
	  		<label>博客内容</label><textarea  v-model="blog.content"></textarea>
	  		<div id="checkboxes">
	  			<label>Vue.js</label><input type="checkbox" value="Vue.js" v-model="blog.categories">
	  			<label>angular4</label><input type="checkbox" value="angular4" v-model="blog.categories">
	  			<label>Node.js</label><input type="checkbox" value="Node.js" v-model="blog.categories">
	  			<label>React.js</label><input type="checkbox" value="React.js" v-model="blog.categories">
	  		</div>

	  		<label>作者：</label>
	  		<select v-model="blog.author">
	  			<option v-for="author in authors">{{author}}</option>
	  		</select>
	  		<button v-on:click.prevent="post">添加博客</button>
  	    </form>
  		<div v-if="submit">
  			<h3>添加成功</h3>
  		</div>
  		<div id="preview">
  			<h3>博客总览</h3>
  			<p>博客标题:{{blog.title}}</p>
  			<p>博客内容:</p>
  			<p>{{blog.content}}</p>
            <span v-for="category in blog.categories">
            	{{category}}
            </span>
            <p>作者：{{blog.author}}</p>
  		</div>
    </div>
</template>

<script>
export default {
	name: 'addBlog',
	data () {
	    return {
	    	blog:{
	    		title:"",
	    		content:"",
	    		categories:[],
	    		author:""
	    	},
	    	authors:["harry","susan","dawei"],
	    	submit:false
	    }
    },
    methods:{
    	post:function () {
    		this.submit=!this.submit
    		this.$http.post("https://wd3930139874jiaqqv.wilddogio.com/posts.json",this.blog).then(function(data){
                console.log(data)
    		})
    	}
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    #addblog{
    	width: 100%;
    	max-width: 600px;
    	margin: 20px auto;
    	padding:20px;
    }
    label{
    	display: block;
    	margin-top: 10px;
    }
    textarea{
    	height: 200px;
    }
    input[type="text"],textarea,select{
    	display: block;
    	width: 100%;
    	padding: 8px;
    }
    #checkboxes label{
    	display: inline-block;
    	margin: 5px;
    }
    #checkboxes input{
    	display: inline-block;
    	margin: 0 8px 0 5px;
    }
    select{
    	padding: 0;
    	margin: 8px 0 8px;
    }
    button{
    	padding: 10px;
    	background: red;
    	color: white;
    	border: 0;
    	border-radius: 4px;
    	cursor: pointer;
    	margin-top: 10px;
    }
    #preview{
    	margin-top: 20px;
    	border: 1px dotted gray;
    	padding: 10px;
    }
</style>



