<template>
  <div id="ShowBlog" v-changWid:bgColor="'wide'">
  	<h2>博客总览</h2>
  	<input type="text" v-model="search" placeholder="搜索">
  	<div v-for="blogItem in filteredBlog" class="blogItem">
  		<router-link v-bind:to="'/blog/'+blogItem.id"><h3 v-rainbow="'wid'">{{blogItem.title | toUpperCase}}</h3></router-link>
  		<article>{{blogItem.content | snippet}}</article>
  	</div>
  </div>
</template>

<script>
export default {
    name: 'ShowBlog',
    data () {
        return {
    	    blogItems:[],
    	    search:""
        }
    },
    created(){
    	this.$http.get("https://wd3930139874jiaqqv.wilddogio.com/posts.json").then(function(data){
    		var blogArray=[];
            for (let key in data.body) {
            	data.body[key].id=key;
            	blogArray.push(data.body[key]);
            }
            this.blogItems=blogArray;
    		// this.blogItems=data.body.slice(0,10);
    	});
    },
    computed:{
    	filteredBlog:function(){
    		return this.blogItems.filter((blog)=>{
                return blog.title.match(this.search);
    		})
    		
    	}
    },
    filters:{
    	toUpperCase:function(value){
    	return value.toLowerCase().replace(/( |^)[a-z]/g, (L) => L.toUpperCase());
    }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    a{
    	text-decoration-line: none;
    }
    #ShowBlog{
    	max-width: 800px;
    /*	width: 700px;*/
    	margin: 0 auto; 
    	padding: 20px;
    }
    .blogItem{
    	padding: 20px;
    	margin: 20px 0;
    	background: lightgray;
    }
    input{
    	width: 50%;
    	padding:8px 0 ;
    }
</style>



