import ShowBlog from './components/ShowBlog'
import SingleBlog from './components/SingleBlog'

export default [
	   {path:"/",component:ShowBlog},
	   {path:"/add",component:addBlog},
	   {path:"/blog/:id",component:SingleBlog}
	]


